<template>
  <b-row>
    <b-col lg="6">
      
        <c-table :table-data="items" :fields="fields" caption="Product Details"></c-table>
      
    </b-col>
      <b-col lg="6">
        <b-card>
          <div slot="header">
            <strong>Update/Delete</strong> Product
          </div>
          <b-form v-on:submit.prevent="updateData">
          <b-form-group
            description="The products full name."
            label="Product Name"
            label-for="basicName"
            :label-cols="3"
            :horizontal="true">
            <b-form-input id="basicName" v-model="posts.product_name" :value="posts.product_name" type="text" autocomplete="name"></b-form-input>
          </b-form-group>
          <b-form-group
            description="The price of the product"
            label="Price"
            label-for="basicText"
            :label-cols="3"
            :horizontal="true">
            <b-form-input id="basicText" v-model="posts.price" :value="posts.price" type="text"></b-form-input>
          </b-form-group>
          <b-form-group
            description="A short description of the product"
            label="Short Description"
            label-for="basicText"
            :label-cols="3"
            :horizontal="true">
            <b-form-input id="basicText" v-model="posts.short_description" :value="posts.short_description" type="text"></b-form-input>
          </b-form-group>

          <b-form-group
            label="Detailed Description"
            label-for="basicTextarea"
            :label-cols="3"
            :horizontal="true">
            <b-form-input v-model="posts.long_description" :value="posts.long_description" id="basicTextarea" :textarea="true" :rows="9" placeholder="Detailed Description of the Product"></b-form-input>
          </b-form-group>


          <b-form-group
            label="Keywords"
            label-for="basicMultiSelect"
            :label-cols="3"
            :horizontal="true">
            <b-form-select id="basicMultiSelect"
              v-model="posts.search_keywords"
              :plain="true"
              :multiple="true"
              :options="[
                {
                  text: 'Please select some item',
                  value: null
                },
                {
                  text: 'This is First option',
                  value: 'a'
                }, {
                  text: 'Default Selected Option',
                  value: 'b'
                }, {
                  text: 'This is another option',
                  value: 'c'
                }, {
                  text: 'This one is disabled',
                  value: 'd',
                  disabled: true
                }]"
              :value="[null,'c']">
            </b-form-select>
          </b-form-group>
          <b-form-group
            label="Search Keywords"
            label-for="basicMultiSelect"
            :label-cols="3"
            :horizontal="true">
            <b-form-select id="basicMultiSelect"
              v-model="posts.keywords"
              :plain="true"
              :multiple="true"
              :options="[
                {
                  text: 'Please select some item',
                  value: null
                },
                {
                  text: 'This is First option',
                  value: 'a'
                }, {
                  text: 'Default Selected Option',
                  value: 'b'
                }, {
                  text: 'This is another option',
                  value: 'c'
                }, {
                  text: 'This one is disabled',
                  value: 'd',
                  disabled: true
                }]"
              :value="[null,'c']">
            </b-form-select>
          </b-form-group>

          <div slot="footer">
            <b-button type="submit" size="sm" variant="primary"><i class="fa fa-dot-circle-o"></i> Submit</b-button>
            <b-button v-on:click="delData()" type="submit" size="sm" variant="danger"><i class="fa fa-ban"></i> Delete</b-button>
          </div>
          </b-form>
        </b-card>
        </b-col>
    </b-row>
</template>

<script>
  import { shuffleArray } from '@/shared/utils'
  import cTable from './Table.vue'
  import axios from 'axios'
  

  var link = window.location.href
  var key = link.split("product/",24).pop()
  console.log(key)

export default {
  name: 'dashboard',
  components: {
    cTable,
  },
  data: function () {
    return {
      items:[],
      itemsArray: [],
      fields: [
        {key: 'product_name', label: 'Product', sortable: true},
        {key: 'subtitle',label: 'Subtitle'},
        {key: 'short_description',label: 'Short Description'},
        {key: 'long_description',label: 'Long Description'}, 
        {key: 'price',label: 'Price'},
        {key: 'status'},

      ],
      selected: 'Month',
      // posts: [],
      errors: [],
      posts: {
                    product_name: "",
                    subtitle: "",
                    short_description: "",
                    long_description: "",
                    price: "",
                    keywords: "",
                    search_keywords:"",
                    _id: key

                },
                response: "",
      
  }

    
  },
  async mounted(){
    axios.get(`https://selacious-cloud-siteapi.herokuapp.com/products/${key}`)
    .then(
      response => {
      this.posts = response.data
    });
    axios.get(`https://selacious-cloud-siteapi.herokuapp.com/products/${key}`)
    .then(
      response => {
      this.itemsArray = response.data
      this.items = [this.itemsArray]
    });
  },  
  methods: {
    variant (value) {
      let $variant
      if (value <= 25) {
        $variant = 'info'
      } else if (value > 25 && value <= 50) {
        $variant = 'success'
      } else if (value > 50 && value <= 75) {
        $variant = 'warning'
      } else if (value > 75 && value <= 100) {
        $variant = 'danger'
      }
      return $variant
    },
    flag (value) {
      return 'flag-icon flag-icon-' + value
    },
    async updateData() {
      axios.put(`https://selacious-cloud-siteapi.herokuapp.com/products/${key}`,this.posts).then((response) =>{
        console.log(response);
        this.$router.push("/product");
      });
    },
    async delData() {
                axios({ method: "DELETE", "url": `https://selacious-cloud-siteapi.herokuapp.com/products/${key}`, "data": this.posts, "headers": { 'Content-Type': 'application/x-www-form-urlencoded' } }).then(result => {
                    this.posts = result.data;
                    this.$router.push("/product");
                }, error => {
                    console.error(error);
                });
    }
  }
}

</script>

